/*   */
import { useEffect, FormEvent } from "react";
import { Link, Navigate } from "react-router-dom";
import {
  Input,
  Button,
  PasswordInput,
} from "@ya.praktikum/react-developer-burger-ui-components";
import { SUCCESS_RESET, resetPassword } from "../../services/actions/user";
import { useForm } from "../../hooks/useForm";
import { useAppDispatch, useAppSelector } from "../../hooks/customHooks";

/*  */
import styles from "./password-reset.module.scss";

/*   */
const staticStrings = [
  " ",
  "",
  " ?",
  "",
];

const PasswordReset = () => {
  const dispatch = useAppDispatch();
  const isSuccessReset = useAppSelector((store) => store.user.reset);
  const { values, handleChange } = useForm();

  useEffect(() => {
    dispatch({
      type: SUCCESS_RESET,
    });
  }, []);

  const handlerSubmit = (e: FormEvent) => {
    e.preventDefault();
    dispatch(resetPassword(values.password, values.code));
  };

  if (isSuccessReset) {
    return <Navigate to="/react-burger/login" />;
  }

  return (
    <form className={styles.passwordReset__content} onSubmit={handlerSubmit}>
      <h2
        className={`text text_type_main-medium ${styles.passwordReset__title}`}
      >
        {staticStrings[0]}
      </h2>
      <PasswordInput
        placeholder={"  "}
        onChange={handleChange}
        value={values.password}
        name={"password"}
        extraClass="mt-6 mb-6"
      />
      <Input
        type={"text"}
        placeholder={"   "}
        value={values.code || ""}
        onChange={handleChange}
        name={"code"}
        error={false}
        errorText={""}
        size={"default"}
        extraClass="mb-6"
      />
      <Button
        htmlType="submit"
        type="primary"
        size="medium"
        extraClass={styles.passwordReset__button}
      >
        {staticStrings[1]}
      </Button>
      <p
        className={`text text_type_main-default text_color_inactive ${styles.passwordReset__text}`}
      >
        {staticStrings[2]}
        <Link to="/react-burger/login" className={styles.passwordReset__link}>
          {" "}
          {staticStrings[3]}
        </Link>
      </p>
    </form>
  );
};

export default PasswordReset;
